import seachInput from '@/components/searchInput.vue';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			currentIndex: 0,
			tabList: ["综合", "销量", "价值"],
			goodsPramas: {
				cid: "",
				query: "",
				pagenum: 1,
				pagesize: 10
			},
			goods: [],
			isShow: false
		}
	},
	onLoad(option) {
		this.goodsPramas.cid = option.cid
		this.getGoodsList()
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.goods = []
		this.goodsPramas.pagenum = 1
		this.getGoodsList()
	},
	components: {
		seachInput,
		uniLoadMore
	},
	methods: {
		handleClick(index) {
			this.currentIndex = index
		},
		// 获取商品列表
		async getGoodsList() {
			const res = await this.$request({
				url: "/v1/goods/search",
				data: this.goodsPramas
			})
			// console.log(res)
			let arr = this.goods
			this.goods = res.goods
			this.isShow = this.goods.length === 0 ? true : false
			this.goods = [...arr, ...this.goods]
			// 数据请求成功后关闭刷新窗口
			uni.stopPullDownRefresh()
		},
		// 滚动触底
		scrolltolower() {
			this.goodsPramas.pagenum++
			if (this.isShow) {
				return
			}
			this.getGoodsList()
		}
		
	}
}
